/* --------------------------------------------------------------------------------------------------------------------------------------- */
/* The CSS Below is for mobile in portrait orientation*/
@media only screen and (max-width: 767px) {
    .cards {
        width: 120%;
        -webkit-transform: rotate(90deg);
        margin-left:-10%;     
    }

    .card_container {
        text-align: center;
        animation: fadeIn 0.5s;
        position: absolute;
        margin-top: 30%;
    }

    .card_title {
        position: absolute;
        color: white;
        top:-30%;
        left:25%;
        text-align: left;
        font-size:5vw;
        filter: drop-shadow(0px 0px 15px #292929);
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        width:50%;
    }


    .card_player_name {
        position: absolute;
        color: rgb(255, 255, 255);
        top: -12%;
        left: 25%;
        font-size: 4vw;
        filter: drop-shadow(0px 0px 15px #000000);
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }

    .card_description {
        position: absolute;
        color: white;
        top: 45%;
        right: 24%;
        font-size: 4vw;
        text-align:center;
        width: 50%;
        filter: drop-shadow(0px 0px 15px #292929);
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        
    }

    .card_description_video {
        position: absolute;
        color: white;
        top: 55%;
        right: 24%;
        font-size: 3vw;
        width: 50%;
        filter: drop-shadow(0px 0px 15px #292929);
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }

    .card_video {
        position: absolute;
        color: white;
        top: 5%;
        right: 24%;
        width:50%;
        height: 38%;
        filter: drop-shadow(0px 0px 15px #292929);
        border: 5px solid rgba(193, 193, 193, 0.8);
    }

    @keyframes fadeIn {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }

    .versus_label {
        font-size: 3vw;
        color: rgba(95, 0, 0, 0.749);
        filter: drop-shadow(0px 0px 15px #ffffff);
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }

    .playing_card1 {
        width:25%;
        position:absolute;
        margin-top:-30%;
        left:15%;
    }

    .playing_card2 {
        width:25%;
        position:absolute;
        margin-top:-30%;
        left:45%;
        cursor: pointer;
    }

    .higher_or_lower_description {
        position: absolute;
        color: white;
        top: 50%;
        left: 24%;
        font-size: 4vw;
        width: 50%;
        filter: drop-shadow(0px 0px 15px #292929);
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }

    .rof_card {
        position: absolute;
        right: 35%;
        width: 30%;
        margin-top: -20%;
        cursor: pointer;
    }

    .rof_description {
        position: absolute;
        color: white;
        margin-top:35%;
        left: 18%;
        font-size: 4vw;
        padding: 1%;
        width: 65%;
        filter: drop-shadow(0px 0px 15px #292929);
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }


    .nat20_container {}

    .nat20 {
        position: absolute;
        top: -5%;
        right: 30%;
        
    }

    .nat1wizard {
        width: 40%;
        position: absolute;
        top: 65%;
        right: 45%;
        filter: drop-shadow(0px 0px 15px #000000);
        animation: fadeIn 0.5s;
    }

    .dice_nat20 {
        color: rgb(0, 0, 0);
        font-size: 1.5vw;
        filter: drop-shadow(0px 0px 15px #ffffff);
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-weight: bold;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        z-index: 10000;
    }

    .dice_nat20_desc {
        font-size: 3vw;
        filter: drop-shadow(0px 0px 15px #292929);
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        color: white;
        padding: 2%;
        position: absolute;
        top: 105%;
        right: 21%;
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        background-color: rgba(0, 0, 0, 0.8);
        border: 2px solid rgb(255, 255, 255);
        padding: 1%;
        width: 55%;
    }


    .dice_description {
        position: absolute;
        color: white;
        top: 48%;
        right: 30%;
        font-size: 3vw;
        width: 40%;
        filter: drop-shadow(0px 0px 15px #292929);
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }

    .dice_roll_desc {
        position: absolute;
        color: white;
        top: 75%;
        right: 30%;
        font-size: 3vw;
        width: 40%;
        filter: drop-shadow(0px 0px 15px #292929);
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }

    .dice_image {
        position: absolute;
        color: white;
        top: -20%;
        right: 20%;
        font-size: 1vw;
        width: 60%;
        filter: drop-shadow(0px 0px 15px #292929);
        cursor: pointer;
    }

    .dice_image[wobble="1"] {
        animation-name: wobble;
        animation-duration: 2s;
    }


    /* Setting Animation effect to the 
     dice using css keyframe */
    @keyframes wobble {
        from {
            transform: translate3d(0, 0, 0);
        }

        15% {
            transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -5deg);
        }

        30% {
            transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, 3deg);
        }

        45% {
            transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -3deg);
        }

        60% {
            transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 2deg);
        }

        75% {
            transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg);
        }

        to {
            transform: translate3d(0, 0, 0);
        }
    }

    .cards_and_button_container {
        position: absolute;
        color: white;
        top: 40%;
        right: 15%;
        filter: drop-shadow(0px 0px 15px #292929);
        width: 70%;

    }

    .check_button {
        display: inline-block;
        padding: 15px 25px;
        font-size: 1vw;
        cursor: pointer;
        text-align: center;
        text-decoration: none;
        outline: none;
        color: #000000;
        background-color: #ffffff;
        border: none;
        border-radius: 15px;
        box-shadow: 0 9px #999;
        margin: 5%;
    }

    .check_button:active {
        background-color: #979797;
        box-shadow: 0 5px #666;
        transform: translateY(4px);
    }
}

